.wptravel-block-wrapper.wptravel-block-tabs {
	border: 1px solid rgba(222, 222, 222, 0.49);

	.wp-travel-block-rich-text {
		border: 1px solid #1e1e1e;
		padding: 10px;
	}

	// Preview.
	&.wptravel-block-preview {
		background-color: #fff;
		&::before {
			content: none;
		}
	}

	// Edit.
	&.wptravel-block-edit {
		.wp-traval-button-danger {
			box-shadow: inset 0 0 0 1px #f44336 !important;
		}
		.button-primary {
			color: #fff !important;
		}

		.wp-travel-trip-edit-menu {
			.components-panel__body {
				&.is-opened {
					.components-panel__body-title {
						background: #e0e0e0;
					}
				}
				.components-panel__row {
					padding: 10px 20px;
					&.wp-travel-has-child-panel {
						.wp-travel-panel-wrap {
							width: 100%;
							.components-panel__body {
								.components-panel__body-title {
									background: #fff;
								}
								&.is-opened {
									.components-panel__body-title {
										background: #e0e0e0;
									}
								}
								.components-base-control__field {
									.components-form-toggle {
										margin-right: -12px;
									}
								}
							}
						}
					}

					.wp-travel-form-input-with-clear {
						position: relative;
						button.wp-travel-form-input-clear-btn {
							position: absolute;
							right: 5px;
							top: 6px;
							background: none;
							padding: 0;
						}
					}
				}
			}
		}
		.components-notice {
			margin: 0;
		}
		.wp-travel-trip-pricings {
			margin-top: 20px;
		}
		.wp-travel-ui.wp-travel-ui-card {
			margin-top: 20px;
		}

		// booking tab dates section scss
		.wp-travel-ui {
			.components-base-control__field {
				justify-content: flex-end;
				span.components-form-toggle {
					margin-right: -12px;
				}
			}

			.components-panel__body {
				.components-panel__row {
					.components-base-control__field {
						input {
							padding: 0;
						}
					}
				}
			}
		}

		// Block Tab Gallery Section
		.wp-travel-advanced-gallery-title-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0.5em 0 1em;
			h3 {
				margin: 0;
			}
			.wp-travel-advance-gallery-buttons {
				button.components-button {
					margin-left: 0.5em;
					padding: 0 12px;
				}
			}
		}
		.wp-travel-advanced-gallery {
			// ul {
			// 	margin: 0;
			// 	padding: 0;
			// }
			li {
				&.featured-image {
					&::before {
						content: "★";
						color: #f1d375;
						position: absolute;
						// left: 30px;
						// left: 50px;
						font-family: dashicons;
						text-shadow: 0px 1px 1px #000;
						// top: 0px;
						z-index: 1;
						font-size: 24px;
						// top: unset;
						bottom: 0;
						left: 265px;
					}
					.wptag__media-card {
						.wptag__thumbnail {
							cursor: default;
						}
					}
					.wptag__media-card {
						.wptag__thumbnail {
							position: relative;
							&::after {
								content: "";
								position: absolute;
								width: 100%;
								height: 100%;
								background: #000;
								opacity: 0.3;
							}
						}
					}
				}

				.wptravel-swap-list {
					display: grid;
					// top: 15px;
					// left: 10px;
					left: 20px;
					top: 50%;
					transform: translate(-50%, -50%);
					button.components-button {
						height: auto;
					}
				}

				.wptag__dragger {
					padding: 0 20px;
					span.dashicons-menu {
						display: none;
					}
				}
				// .wptag__dragger {
				// 	position: absolute;
				// 	top: 0;
				// 	left: 0;
				// 	z-index: 11;
				// 	background-color: #fff;
				// 	border: 1px solid #ccc;
				// 	padding: 2px;
				// 	.dashicons,
				// 	.dashicons-menu {
				// 		&::before {
				// 			content: "\f545";
				// 		}
				// 	}
				// }
				.wptag__media-card {
					flex: 1;
					display: flex;
					.wptag__thumbnail {
						width: 250px;
						height: unset;
						margin-right: 1em;
						cursor: pointer;
						.components-button {
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							background-color: #007cba;
							border-radius: 0;
							color: #fff;
							justify-content: center;
							border: 1px solid #007cba;
							box-shadow: none;
						}
					}
					.wptag__media-detail-form {
						flex: 1;
						.components-base-control {
							width: 100% !important;
							&:nth-child(2) {
								margin-bottom: 0;
								textarea,
								.components-base-control__field {
									margin-bottom: 0;
								}
							}
						}
						.wptag__card-remove {
							padding: 3px 10px;
							color: #f75858;
							line-height: 26px;
							background: none;
							top: 5px;
							right: 5px;
							border-radius: 3px;
						}
					}
				}
			}
		}
	}

	.wp-travel-trip-edit-block-tab-list {
		ul {
			display: flex;
			margin: 0 !important;
			padding: 0 !important;
			background: #f1f1f1;

			li {
				align-items: center;
				display: flex;
				flex: 1;
				flex-direction: column;
				font-size: 16px;
				list-style: none;
				// padding-left: 5px;
				padding: 12px;
				border-right: 1px solid rgba(222, 222, 222, 0.49);
				background: #f9f9f9;
				&:last-child {
					border-right: none;
				}
				button.components-button {
					padding: 0;
				}

				&.active-tab {
					background: #fff;
					button {
						color: #000;
					}
				}
				button {
					display: block;
					width: 100%;

					&:focus:not(:disabled) {
						box-shadow: none;
						outline: none;
					}
				}
				.tabs-toolbar {
					display: flex;
					visibility: hidden;
					button {
						span {
							font-size: 10px;
						}
					}
				}
				&:hover {
					.tabs-toolbar {
						visibility: visible;
					}
				}

				p {
					line-height: 1;
					margin: 0;
					padding: 0;
				}
			}
		}
	}

	// Content Part
	.wptravel-tab-content-wrapper {
		background: #fff;
		padding: 20px;
		p.wp-travel-block-rich-text {
			border-color: #909090;
		}
		.button {
			width: auto;
		}
		.wp-travel-sortable-component {
			&.itinerary-sortable {
				.wptravel-swap-list {
					position: absolute;
					top: 26px;
					left: 40px;
					button.components-button {
						padding: 0;
					}
					button[disabled] {
						border: none;
						background: none;
						color: #1e1e1e;
					}
				}
				.components-panel__body {
					h2.components-panel__body-title {
						button.components-button:focus:not(:disabled) {
							box-shadow: none;
						}
					}
					.components-panel__row {
						&.itinerary-description {
							flex-direction: row-reverse;
							.components-base-control {
								width: 60%;
							}
						}
						// button.components-button {
						// 	// color: #f44336;
						// }
						.components-dropdown {
							.components-base-control {
								width: auto;
							}
						}
						label {
							font-size: 13px;
						}
					}
				}
			}
		}

		.wp-travel-ui {
			ul.wp-travel-gallery-list {
				padding: unset;
				li.gallery-item {
					list-style: none;
					&::before {
						top: unset;
						left: unset;
						right: 2px;
						bottom: 2px;
					}
					.wptravel-swap-list {
						right: unset;
						left: 30px;
						button.components-button {
							padding: 5px;
							&:first-child {
								top: -5px;
								left: -23px;
								&:hover {
									color: #007cba;
								}
							}
							&:last-child {
								top: -5px;
								left: 16px;
								&:hover {
									color: #007cba;
								}
							}
						}
					}

					button.components-button {
						color: #2f2c2c;
						background: #fff;
						padding: 1px 5px;
						top: 7px;
						right: 7px;
						border-radius: 0;
						display: none;
						&:hover {
							color: #e8213d;
						}
					}
					&:hover {
						.wptravel-swap-list {
							button.components-button {
								padding-bottom: 2px;
								> svg {
									padding-top: 0;
								}
							}
						}
						button.components-button {
							display: block;
							padding-bottom: 1px;
							> svg {
								padding-top: 2px;
							}
						}
					}
				}
			}
		}

		.wp-travel-trip-edit-menu {
			hr {
				border-color: #dedede;
			}
			.components-tab-panel__tabs {
				button.components-button {
					&.active-tab {
						border-bottom: 2px solid #2271b1;
						box-shadow: none;
						outline: none;
						color: #2271b1;
					}
					&.components-tab-panel__tabs-item:focus {
						box-shadow: none;
					}
				}
			}
			.components-tab-panel__tab-content {
				.wp-travel-ui {
					h4 {
						font-size: 13px;
						margin-bottom: 0;
					}
					.components-panel__row {
						label {
							font-size: 13px;
						}
					}
					.components-panel__body {
						.components-panel__row {
							.text-right {
								p.components-form-token-field__help {
									font-size: 13px;
									margin: 0;
								}
							}
							.wp-travel-form-input-with-clear {
								button.wp-travel-form-input-clear-btn {
									i.fa-times-circle {
										font-size: 17px;
										color: #ff4545;
									}
								}
							}
						}
						.components-panel__body-title {
							padding-left: 15px;
							button.components-button {
								padding: 15px;
							}
						}
					}
				}
				.wp-travel-trip-pricings {
					.wp-travel-sortable-component {
						.components-panel__body {
							h2.components-panel__body-title {
								button.components-button {
									padding: 15px;
								}
							}
							&.is-opened {
								.components-panel__row {
									.wp-travel-field-value {
										.wp-travel-select-wrapper {
											> div {
												> div {
													height: 38px;
													> div {
														height: 38px;
														> div {
															font-size: 13px;
														}
													}
												}
											}
										}
										p.description {
											.wp-travel-upsell-badge {
												color: #5cb85c !important;
												&:hover {
													color: #fff !important;
												}
											}
										}
									}
									&.wp-travel-has-child-panel {
										.components-panel__body.is-opened {
											.components-panel__row {
												.components-base-control__field {
													.components-input-control__container {
														select.components-select-control__input {
															padding-top: 2px;
															padding-bottom: 2px;
														}
													}
												}
											}
										}
									}
								}
							}
							.components-panel__body-title {
								button.components-button {
									> svg {
										display: none;
									}
								}
							}
						}
					}
					.components-panel__row.wp-travel-action-section {
						margin: 15px 0;
					}
				}
			}

			.wp-travel-trip-pricings {
				.wp-travel-select-pricing {
					margin-bottom: 25px;
				}
				.wp-travel-select-wrapper {
					width: 20%;
				}
				.wp-travel-sortable-component {
					.wp-travel-field-value {
						width: 20%;
						.wp-travel-select-wrapper {
							width: 100%;
						}
					}

					.components-panel__row {
						.text-right {
							.components-base-control__field {
								justify-content: flex-end;
								span.components-form-toggle {
									margin-right: 0;
								}
							}
						}
					}
				}
			}
		}

		.wp-travel-trip-faq {
			.wp-travel-sortable-component {
				margin-top: 15px;
				.wptravel-swap-list {
					left: 38px;
					top: 26px;
					button[disabled] {
						border: none;
						background: none;
						color: #1e1e1e;
					}
				}
				.components-panel__body {
					.components-panel__row {
						.components-base-control {
							.components-base-control__field {
								textarea.components-textarea-control__input {
									width: 165px;
								}
							}
						}
					}
				}
			}
		}
	}
}

.block-editor {
	.wp-travel-ui {
		.itinerary-sortable {
			.components-popover__content {
				width: 300px !important;
				left: -375px !important;
				// margin-left: -370px !important;
				.wp-travel-dropdown-content-wrap {
					label {
						float: right;
					}
				}
			}
		}
	}
}

.block-editor {
	.block-editor__container {
		.components-dropdown__content {
			.components-popover__content {
				width: 300px !important;
				left: -325px !important;
				// margin-left: -370px !important;
				.wp-travel-dropdown-content-wrap {
					label {
						float: right;
					}
				}
			}
		}
	}
}

@import "./editor-inspector-control.scss";
