#editor {
	&.block-editor__container {
		.interface-interface-skeleton__sidebar {
			.edit-post-sidebar {
				.components-panel {
					.block-editor-block-inspector {
						.wp-travel-select-wrapper {
							width: 140px;
						}
						div {
							// width: 140px;
							&:first-child {
								// border-top: none;
							}
						}
						.wp-travel-trip-fact {
							.components-panel__body {
								.components-button:not(.wp-traval-button-danger) {
									padding-left: 50px;
									svg.components-panel__icon {
										display: none;
									}
								}
								.components-text-control__input {
									width: 140px;
								}
							}

							.wp-travel-sortable-component {
								margin-top: 15px;
							}
						}
					}
					.components-panel__body {
						border: none;
						border-top: 1px solid #e0e0e0;
						border-bottom: 1px solid #e0e0e0;
						// border-bottom: none;
						margin-bottom: 0;
						&.is-opened {
							// padding: 0;
							h2.components-panel__body-title {
								background: #fff;
							}
						}

						// .editor-post-link {
						// 	// padding: 15px;
						// }
						// h3.edit-post-post-link__preview-label {
						// 	margin-bottom: 10px;
						// 	padding: 0 15px;
						// }
						// .edit-post-post-link__preview-link-container {
						// 	padding: 0 15px 20px;
						// }
						// .editor-post-featured-image {
						// 	padding: 15px;
						// }
						// .editor-post-excerpt {
						// 	padding: 15px;
						// }
						
					}

					.block-editor-block-inspector {
						.components-panel__body {
							.wp-travel-trip-tabs {
								.wp-travel-sortable-component {
									.components-panel__body {
										margin: 0;
										margin-bottom: 8px;
										border: 1px solid #e0e0e0;
									}
								}
							}
						}

						.block-editor-panel-color-gradient-settings {
							.block-editor-color-gradient-control {
								fieldset {
									.components-circular-option-picker__option-wrapper {
										.components-button.components-circular-option-picker__option {
											&:hover {
												> span {
													display: none !important;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
