[data-type="wptravel/trips-list"] {
	.wp-travel-itinerary-items {
		ul {
			display: grid;
			padding: 0;
			grid-gap: 35px;
			grid-template-columns: repeat(3, 1fr);
			@media (max-width: 1199px) {
				grid-template-columns: repeat(2, 1fr);
				grid-gap: 25px;
				padding: 0 15px;
			}
			@media (max-width: 575px) {
				grid-template-columns: repeat(1, 1fr);
			}
			li {
				list-style: none;
				border: 1px solid #e0e0e0;
				// margin-right: 25px;
				padding-bottom: 25px;
				.wp-travel-post-item-wrapper {
					.wp-travel-post-wrap-bg {
						.wp-travel-post-thumbnail {
							img.wp-post-image {
								width: 100%;
								max-width: 100%;
								height: auto;
							}
						}
						.wp-travel-savings {
							right: -8px;
							top: 20px;
							bottom: unset;
						}
					}
					.wp-travel-post-info {
						display: flex;
						justify-content: space-between;
						// align-items: center;
						flex-wrap: wrap;
						padding-inline: 15px;
						align-items: flex-start;
						&::before {
							content: none;
						}
						h4.post-title {
							padding: 0 15px;
							margin: 0;
							width: 70%;
						}
						.recent-post-bottom-meta {
							width: 30%;
							text-align: right;
						}
						.wp-travel-trip-detail {
							padding: 0 15px;
							.trip-price {
								span.person-count {
									display: block;
									text-align: right;
								}
							}
						}
						&.clearfix {
							&::after {
								content: none;
							}
						}
					}

					.wp-travel-post-content {
						padding: 0 15px;
					}
				}
			}
		}
	}
}
.entry-meta {
	.wp-travel-average-review {
		font-family: "Font Awesome 5 Free";
		font-size: 1em;
		height: 1em;
		line-height: 1;
		overflow: hidden;
		position: relative;
		width: 5.5em;
		display: inline-block;
		&::before {
			// color: #f9a032;
			content: "\f005\f005\f005\f005\f005";
			float: left;
			left: 0;
			position: absolute;
			top: 0;
		}
		> span {
			color: var(--travel-log-primary-color);
			float: left;
			left: 0;
			overflow: hidden;
			padding-top: 1.5em;
			// position: absolute;
			top: 0;
			/* color: #f9a032; */
			font-weight: 900;
			position: relative;
			// display: none;
			&::before {
				content: "\f005\f005\f005\f005\f005";
				left: 0;
				position: absolute;
				top: 0;
			}
		}
	}

	.category-list-items {
		position: relative;
		float: right;
		margin-right: 0;
		padding: 2px 0;
		.wp-travel-caret {
			padding-left: 7px;
			display: inline-block;
			cursor: pointer;
			.sub-category-menu {
				visibility: hidden;
				opacity: 0;
				position: absolute;
				top: 40px;
				right: 0px;
				text-align: left;
				background: #fff none repeat scroll 0 0;
				box-shadow: 0 6px 12px #c7c7c7;
				min-width: 115px;
				// padding: 10px;
			    padding: 0;
				width: 100%;
				transform: translateY(-2em);
				z-index: 2;
				transition: all 0.3s ease-in-out 0s,
					visibility 0s linear 0.3s,
					z-index 0s linear 0.01s;
			        a {
						padding: 6px 10px;
						border-bottom: 1px solid #eee;
						cursor: pointer;
						display: block;
					}
			}
			&:hover {
				.sub-category-menu {
					visibility: visible;
					opacity: 1;
					z-index: 2;
					top: 30px;
					transform: translateY(0%);
					transition-delay: 0s, 0s, 0.3s;
				}
			}
		}
	}
}
