.wptravel-block-wrapper {
	&.wptravel-block-breadcrumb {
		// padding: 20px 0;
		ul {
			display: flex;
			flex-direction: row;
			list-style: none;
			align-items: center;
			padding-left: 0;
			justify-content: flex-start;
			li {
				position: relative;

				&::after {
					content: "\00BB";
					position: absolute;
					font-family: wt-icon-solid;
					top: 50%;
					right: -7px;
					transform: translate(0, -50%);
					@media (max-width: 739px) {
						top: 14px;
					}
				}
				&:nth-last-child(1) {
					&::after {
						display: none;
					}
					span {
						// margin-left: 15px;
						text-transform: capitalize;
					}
				}
			}
		}
	}
}

// edit view for breadcrumb
.wptravel-block-wrapper.wptravel-block-edit.wptravel-block-breadcrumb {
	display: flex;
	justify-content: center;
	.components-base-control__field {
		width: 50%;
		padding: 0 20px;
		fieldset {
			legend {
				.block-editor-color-gradient-control__color-indicator {
					.components-base-control__label {
						color: black;
						padding: 0 10px;
					}
				}
			}
			.components-circular-option-picker {
				.components-circular-option-picker__custom-clear-wrapper {
					justify-content: unset;
				}
			}
		}
	}
}
