@import "./node_modules/react-datepicker/src/stylesheets/datepicker.scss";

// Backend calendar block next and previous button css fix
#wptravel-block-trip-calendar {
	&.wptravel-block-wrapper.wptravel-block-preview {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 1.5rem;
		.wp-travel-booking__header {
			.wp-travel-date-picker-btn {
				display: inline-flex;
				align-items: center;
				padding: 0 0 0 1em;
				overflow: hidden;
				font-weight: bold;
				white-space: nowrap;
				span {
					display: inline-block;
					margin-left: 1em;
					padding: 0.75em 1em 0.5em;
					background: rgba(0, 0, 0, 0.3);
					svg {
						width: 15px;
						height: 15px;
						fill: #fff;
					}
				}
			}
		}
		> p {
			font-size: 14px;
			color: #3399ff;
			display: inline-block;
			margin: 0;
			padding: 0.5em;
			border: 1px solid #3399ff;
			background-color: rgba(51, 153, 255, 0.2);
			border-radius: 4px;
			position: relative;
			animation: arrowAnimate 1s ease infinite;
			&::before,
			&::after {
				content: "";
				border-top: 8px solid transparent;
				border-bottom: 8px solid transparent;
				border-right: 8px solid #d3e9ff;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translate(-100%, -50%);
			}
			&::before {
				border-right: 8px solid #3399ff;
				left: -1px;
			}
		}
	}
	button.react-datepicker__navigation {
		position: absolute;
		border: none;
		&.react-datepicker__navigation--previous {
		}
		&.react-datepicker__navigation--next {
			left: unset;
			right: 0;
			margin-right: 0;
		}
	}
}

@keyframes arrowAnimate {
	0% {
		transform: translateX(0);
	}
	60% {
		transform: translateX(-5px);
	}
	100% {
		transform: translateX(0);
	}
}
