.wptravel-block-wrapper {
	.button {
		width: 100%;
	}

	form.wp-travel-enquiries-form {
		.wp-travel-form-field {
			// width: calc(90% + 32px);
			width: 100%;
			&.wp-travel-enquiry-gdpr-section {
				.radio-checkbox-label {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					gap: .4em;
					input[type="checkbox"] {
						display: inline-block;
					}
				}
			}
			&.button-field {
				width: 100%;
				// width: calc(100% - 650px);
				margin-top: 10px;
			}
		}
	}
	&.wptravel-block-trip-enquiry {
		form {
			input:not([type="checkbox"]), textarea {
				width: 100%;
			}
		}
	}
	&.wptravel-block-preview {
		&.wptravel-block-trip-enquiry {
			form.wp-travel-enquiries-form {
				.wp-travel-form-field.wp-travel-enquiry-subscribe-section {
					label.radio-checkbox-label {
						display: flex;
						align-items: center;
						input {
							width: auto;
							margin-right: 6px;
						}
					}
				}
			}
		}
	}
}
