.wptravel-templates-modal {
	min-width: 800px;
	max-width: 800px;
	.components-modal__header:first-child {
		display: none;
	}
}
.wptravel-plugin-templates-categories-row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	margin: 10px 0;
}
.wptravel-plugin-templates-list {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	margin-top: -15px;
	margin-left: -15px;
	list-style: none;

	> li {
		-webkit-box-flex: 0;
		-ms-flex: 0 0 33.33%;
		flex: 0 0 33.33%;
		width: 33.33%;
		padding-top: 15px;
		padding-left: 15px;
		> button {
			display: block;
			width: 100%;
			padding: 0;
			cursor: pointer;
			background: none;
			background-color: #fff;
			border: none;
			border-radius: 4px;
			-webkit-box-shadow: 0 0 0 1px #edeff0;
			box-shadow: 0 0 0 1px #edeff0;
			-webkit-transition: 0.2s border-color, 0.2s box-shadow;
			-o-transition: 0.2s border-color, 0.2s box-shadow;
			transition: 0.2s border-color, 0.2s box-shadow;

			&:hover {
				box-shadow: 0 0 0 2px #007cba;
			}

			> .wptravel-plugin-templates-list-item-image {
				position: relative;
				display: block;
				.wptravel-plugin-templates-list-item-image-sizer {
					width: 100%;
				}
				.lazyload-wrapper {
					img {
						position: absolute;
						top: 0;
						left: 0;
						height: 100%;
						display: block;
						width: 100%;
						height: auto;
						border-top-left-radius: 4px;
						border-top-right-radius: 4px;
					}
				}
			}
			.wptravel-plugin-templates-list-item-title {
				padding: 10px;
				font-size: 10px;
				font-weight: 600;
				text-transform: uppercase;
				border-top: 1px solid #f8f9f9;
			}
		}
	}
}
