@import "./common.scss";
@import "../blocks/**/editor.scss";
@import "../components/**/editor.scss";
@import "../extensions/**/editor.scss";

@import "./fix-wp-travel-block-style.scss";

.wptravel-block-edit {
	background: #fff;
	border: 1px solid #dedede;
	margin-bottom: 20px;
	.wptravel-edit-options {
		padding-top: 15px;
	}
}
.components-button.wp-traval-button-danger.is-secondary {
	color: #f44336;
	&:hover {
		color: #f44336;
	}
}
.wptravel-block-wrapper {
	label {
		font-weight: bold;
	}
	&.wptravel-block-edit {
		background: #fff;
		border: 1px solid #dedede;
		.wptravel-swap-list {
			// top: 21px;
			// top: 26px;
			top: 24px;
			right: unset;
			// left: 40px !important;
			position: absolute;
			button.components-button {
				padding: 0;
			}
		}
		.components-panel__body {
			border: 1px solid #ededed;
			margin-bottom: 10px;
		}
		ul.wp-travel-gallery-list {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-auto-rows: 1fr;
			grid-gap: 15px;
			margin-bottom: 0;
			li.gallery-item {
				margin: 0;
				height: 100%;
				cursor: pointer;
				border: 1px solid transparent;
				.wptravel-swap-list {
					bottom: unset;
					top: 22px;
					left: 40px;
					right: unset;
					display: block !important;
					position: absolute;
					z-index: 111;
					cursor: pointer;
					height: 24px;
					.components-button {
						padding: 0 5px;
						&:first-child {
							background: #fff;
							padding: 5px;
							color: #2f2c2c;
							position: absolute;
							// left: -33px;
							// top: -15px;
							top: -3px;
							left: -23px;
						}
					}
				}
				figure {
					margin: 0;
					height: 100%;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.components-button {
					padding: 0 5px !important;
					right: 7px;
					// display: none;
					display: block;
					border-radius: 0 !important;
					position: absolute;
				}
			}
		}
		.dropzone {
			margin-top: 25px;
			border: 2px dashed #d0d0d0;
			padding: 40px;
			text-align: center;
			span.uploader-info {
				font-size: 20px;
			}
			.uploader-buttons {
				margin-top: 10px;
				button {
					&.components-button.is-secondary {
						margin-right: 10px;
					}
				}
			}
		}
		.wptravel-tab-content-wrapper {
			// .wp-travel-trip-pricings {
			// 	.wp-travel-sortable-component {
			h2.components-panel__body-title {
				button.components-button:focus:not(:disabled) {
					box-shadow: none;
				}
			}
			// 	}
			// }
		}
	}

	.description {
		float: unset;
	}
	&[class*="is-content-justification-"] {
		display: flex;
		&.is-content-justification-center {
			justify-content: center;
		}
		&.is-content-justification-left {
			justify-content: left;
		}
		&.is-content-justification-right {
			justify-content: right;
		}
	}
	.wptravel-block-edit {
		background: #fff;
		// padding: 10px 15px;
		padding: 15px 25px;
		// &.wptravel-block-trip-filters {
		// 	// padding: 15px 25px;

		// }
		.wptravel-edit-options {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}
		strong {
			display: block;
			margin-bottom: 15px;
		}
		.components-base-control {
			width: 50%;
		}
	}
	&.wptravel-block-preview {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			right: 0;
			z-index: 1;
		}
		.wp-travel-itinerary-items {
			.wp-travel-form-field {
				select {
					padding: 0 10px;
				}
				&.wp-travel-trip-duration {
					width: 100%;
					> span {
						display: inline-block;
						// width: 42%;
						width: 47%;
						// margin-right: 31px;
						margin-right: 0;
						&:last-child {
							margin-left: 10px;
						}
					}
				}
			}
		}
	}
	.components-panel__row.wp-travel-action-section {
		padding: 1em 0;
	}
	.wp-travel-sortable-component {
		.components-panel__row {
			label {
				font-size: 13px;
			}
			.wp-travel-select-wrapper {
				div {
					font-size: 13px;
				}
			}
		}
		.wptravel-swap-list {
			right: unset;
			// top: 28px;
			// left: 32px;
			left: 38px;
			top: 25px;
			transform: translate(-50%, -50%);
			button.components-button[disabled] {
				border: none;
				color: rgb(136, 136, 136);
			}

			.twentytwentyone-supports-dark-theme & {
				top: 24px;
				left: 40px;
			}
		}
		.components-panel__body {
			background: #fff;
			h2.components-panel__body-title {
				padding: 0 20px 0 60px !important;
				button.components-button {
					padding: 16px 10px;
					span {
						> svg {
							margin-right: 0;
							right: 0;
						}
					}
					> svg {
						display: none;
					}
				}
			}
		}
	}

	// Block Tabs
	&.wptravel-block-tabs.wptravel-block-edit {
		.wp-travel-trip-pricings {
			.components-panel__body {
				background: #fff;
				h2.components-panel__body-title {
					padding: 0 20px !important;
				}
			}
		}
	}
}

