.wptravel-block-wrapper {
	&.wptravel-block-trip-categories {
		&.wptravel-block-preview {
			a {
				position: relative;
				&::after {
					content: ", ";
				}
				&:last-child {
					&::after {
						content: none;
					}
				}
			}
		}

		&.wptravel-block-edit {
			background: none;
			border: none;
			.components-base-control__field {
				.components-input-control__container {
					select.components-select-control__input {
						height: 35px;
						min-height: 35px;
					}
				}
			}
		}
	}
}
