.wptravel-container {
	.block-list-appender {
		margin-left: auto;
		margin-right: auto;
	}
}

.wptravel-block-wrapper.wptravel-block-map {
	// Map Preview in Block Section Backend
	&.wptravel-block-preview {
		// &::before {
		// 	content: none;
		// }
		&.wp-block-wptravel-map {
			margin: 40px 0;
			z-index: 1;
			.wp-travel-map-wrap {
				width: 100%;
			}
			.wp-travel-here-map {
				.wp-travel-map {
					.wp-travel-map__search {
						span.mq-place-search {
							input {
								padding: 6px 40px 6px 15px;
								min-height: auto;
								height: auto;
							}
						}
						button.mq-input-icon {
							width: 25px;
							height: 25px;
							bottom: unset;
							top: 9px;
							right: 9px;
							svg {
								right: 4px;
							}
						}
					}
				}
			}
		}

		& > .components-notice {
			display: none;
		}
		& > .components-panel__row {
			&:not(:last-child) {
				display: none;
			}
			.wp-travel-map-wrap {
				width: 100%;
			}
		}
		& > br {
			display: none;
		}
	}

	// Map edit mode scss changes
	&.wptravel-block-edit {
		padding: 15px;
		.components-notice {
			margin: 0;
		}
		.components-panel__body {
			margin-top: 15px;
			margin-bottom: 0;
			.components-panel__body-title {
				// padding: 10px;
				padding: 0;
				button.components-button {
					padding: 16px;
					&:focus:not(:disabled) {
						box-shadow: none;
					}
				}
			}
			.components-notice {
				margin: 15px 0;
			}
			&.is-opened {
				.components-panel__body-title {
					background: #e0e0e0;
				}
			}
		}
	}
}
.components-notice {
	margin: 0;
}
