.block-editor-block-inspector {
	.wp-travel-trip-tabs {
		.wp-travel-sortable-component {
			.wptravel-swap-list {
				display: none;
			}
			.components-panel__body {
				margin: 0 10px 8px 10px;
				border: 1px solid #e0e0e0;
				h2.components-panel__body-title {
					background: #fff;
					padding-left: 0 !important;
					button.components-button {
						// padding: 9px 25px;
						padding: 9px 25px 9px 45px;
						span {
							svg {
								width: 20px;
								height: 20px;
							}
						}
						> svg {
							width: 18px;
							height: 18px;
							position: absolute;
							left: 10px;
						}
					}
				}
			}
		}
	}

	.components-panel__body {
		border: 1px solid #e0e0e0;
		.wp-travel-trip-tabs {
			padding: 15px;
		}
		.components-panel__row {
			// padding: 20px !important;
			.components-base-control {
				&:last-child {
					margin-bottom: 0;
				}
				.components-base-control__field {
					display: block;
					span.components-form-toggle {
						margin-right: 0;
					}
				}
			}
		}
	}
}

