	// Search form widget.
	.wp-travel-search {
		form {
			display: grid;
			grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
			grid-gap: 1rem;
			align-items: end;
		}
		p {
			margin: 0;
			padding: 0;
		}
		label {
			display: inline-block;
		}
	}
