.wptravel-block-wrapper {
	// margin-top: 25px;
	&.wptravel-block-trip-fact {
		&.wptravel-block-preview {
			.tour-info {
				.tour-info-box {
					border: 1px solid #e0e0e0;
					background: #f1f1f1;
					padding: 20px 25px 0;

					.tour-info-column {
						display: flex;
						flex-wrap: wrap;
						align-items: center;
						.tour-info-item {
							width: 33.3333333333%;
							padding-bottom: 20px;
							i {
								padding-right: 10px;
							}
							.fact-value {
								position: relative;
								&::after {
									content: ", ";
								}
								&:last-child {
									&::after {
										content: none;
									}
								}
							}
						}
					}
				}
			}
		}
		&.wptravel-block-edit {
			&[class*="is-content-justification-"] {
				// display: flex;
				display: block;
				padding: 20px;
				&.is-content-justification-center {
					// justify-content: center;
					text-align: center;
				}
				&.is-content-justification-left {
					// justify-content: flex-start;
					text-align: left;
				}
				&.is-content-justification-right {
					// justify-content: flex-end;
					text-align: right;
				}
			}
			.wp-travel-trip-fact {
				.wp-travel-select-wrapper {
					width: 250px;
				}
			}
		}
		.components-button:focus {
			box-shadow: none;
		}
	}
}

.wp-travel-trip-fact {
	.wptravel-swap-list {
		position: absolute;
		top: 9px;
		.components-button {
			padding: 0;
		}
	}
}
